// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import bindTemplateModal from '@/components/modal/alarmOptionModal/bindTemplateModal';
import {
  BTN_TEXT,
  CONSTANTS_CHECK,
  STATUS_CODE,
  TITLE
} from '@/utils/constantsFile';
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  warningTemplateGetAll,
  getPageWarningTemplateWorkshopRelationship,
  insertWarningTemplateWorkshopRelationship
} from '@/api/alarmconfiguration.js';

export default {
  components: {
    bindTemplateModal,
    HeaderTitle,
    HeadOperationItem
  },
  props: {},
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      BTN_TEXT: BTN_TEXT,
      // 显示模态框
      showbindMaintainceTemplate: false,
      // table动画加载
      tableLoading: false,
      // table数据
      linkToolTable: [],
      // 分页配置
      linkToolTablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500]
      },
      // 模态框数据
      templateData: [],
      activeLinkToolItem: null
    };
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description:  获取设备类型关联保养模板列表
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    getWorkshopTypeRelationshipTemplateData() {
      this.tableLoading = true;
      var params = {};
      if (this.linkToolTablePage) {
        params = {
          current: this.linkToolTablePage.currentPage,
          size: this.linkToolTablePage.pageSize
        };
      }
      getPageWarningTemplateWorkshopRelationship(params)
        .then(res => {
          this.tableLoading = false;
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.linkToolTable = res.rows;
            this.linkToolTablePage.total = res.total;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch(err => {
          this.tableLoading = false;
          showErrorNotify(err);
        });
    },
    //
    /**
     * @description:  打开关联保养模板模态框
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    showBindMaintainceTemp() {
      var row = {};

      if (!this.$refs.mmmTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.mmmTable.getRadioRecord();
      }

      this.activeLinkToolItem = row;
      warningTemplateGetAll()
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.templateData = res.rows;
            this.templateData.unshift({
              templateId: 0,
              templateName: '不绑定'
            });
            if (row.templateIdList) {
              this.$refs.bindTemplate.value = row.templateIdList[0];
            } else {
              this.$refs.bindTemplate.value = null;
            }
            this.showbindMaintainceTemplate = true;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
    },
    /**
     * @description: 模态框关闭
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    clickBindMaintainceTempCancel() {
      this.showbindMaintainceTemplate = false;
    },
    cellClickEvent({ column }) {
    },
    /**
     * @description: 保存关联
     * @param data 数据
     * @return: 无
     * @author: ZJM
     */
    submitMaintanceLinkTem(data) {
      const param = {
        typeId: this.activeLinkToolItem.typeId
      };
      if (data) {
        param.templateIdList = [data];
      }
      insertWarningTemplateWorkshopRelationship(
        param
      )
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.clickBindMaintainceTempCancel();
            this.getWorkshopTypeRelationshipTemplateData();
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
    },
    /**
     * @description: 分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    handleTemplateTablePageChange({ currentPage, pageSize }) {
      this.linkToolTablePage.currentPage = currentPage;
      this.linkToolTablePage.pageSize = pageSize;
      this.getWorkshopTypeRelationshipTemplateData();
    },
    refreshTable() {
      this.getWorkshopTypeRelationshipTemplateData();
    }
  },
  mounted() {
    this.getWorkshopTypeRelationshipTemplateData();
  }
};
