<template>
  <el-dialog
    class="bind-maintaince-template"
    width="400px"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content">
    <el-label>告警模板：</el-label>
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in itemsList"
          :key="item.templateId"
          :label="item.templateName"
          :value="item.templateId"
        ></el-option>
      </el-select>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="confirmAction">{{confirmLabel}}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
    </div>
  </el-dialog>
</template>
<script>
// 导入文字常量
import { BTN_TEXT, CONSTANTS_CHECK } from '@/utils/constantsFile.js';
export default {
  name: 'bindMaintainceTemplateModal',
  props: {
    // 模态框名字
    title: { type: String, default: '' },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 数据
    itemsList: { type: Array, default: () => [] },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function
  },
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      BTN_TEXT: BTN_TEXT,
      value: null
    };
  },
  created() {
    this.itemsList.forEach(function(item) {
      if (item.selected) {
        this.value = item.templateId;
      }
    });
  },
  methods: {
    /**
     * @description: 处理点击取消
     * @return: void
     */
    cancelAction: function() {
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
     */
    confirmAction: function() {
      this.handleConfirm(this.value);
    },
    /**
     * @description: 处理点击关闭
     * @return: void
     */
    closeAction: function() {
      if (this.handleClose) {
        this.handleClose();
      }
    }
  }
};
</script>
