// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
import createTemplateModal from '@/components/modal/alarmOptionModal/createTemplateModal';
import createTemplateRelationModal from '@/components/modal/alarmOptionModal/createTemplateRelationModal';
import Sortable from 'sortablejs';
import {
  MSG,
  BTN_TEXT,
  STATUS_CODE,
  TITLE,
  CONSTANTS_CHECK
} from '@/utils/constantsFile';
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  insertWarningTemplateConfigRelationships,
  warningTemplateConfigRelationshipDeleteById,
  warningConfigGetByConditionAction,
  warningTemplateInsert,
  warningTemplateUpdateById,
  warningTemplateGetList,
  warningTemplateDelete,
  getPageWarningConfig
} from '@/api/alarmconfiguration.js';
export default {
  components: {
    createTemplateModal,
    createTemplateRelationModal,
    // 加载table组件
    VXETable,
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem
  },
  props: {},
  data() {
    return {
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // PAGE_TITLE: PAGE_TITLE,
      BTN_TEXT: BTN_TEXT,
      // EQUIPMENT_DATA: EQUIPMENT_DATA,
      // 模态框标题
      templateEditTitle: '',
      // 模态框标题
      itemRealtionTitle: '添加告警项',
      // 是否显示模态框
      showCreateTemp: false,
      // table加载动画
      tableloading: false,
      activeTemplateId: '',
      // 是否新增
      isAddItem: true,
      // 选中数据
      selectTemplate: null,
      // 勾选数据
      selectRecords: [],
      // table配置
      templateTableData: {
        data: [],
        page: {
          total: 10,
          currentPage: 1,
          pageSize: 10,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: [
            'Sizes',
            'PrevJump',
            'PrevPage',
            'Number',
            'NextPage',
            'NextJump',
            'FullJump',
            'Total'
          ]
        },
        columns: [
          {
            type: 'radio',
            align: 'center',
            visible: true,
            width: '50'
          },
          { type: 'checkbox', width: '50', align: 'center' },
          {
            field: 'templateName',
            title: CONSTANTS_CHECK.TEMPLATE_NAME,
            align: 'center'
          },
          {
            field: 'templateRemark',
            title: '备注',
            align: 'center'
          },
          {
            field: '',
            title: BTN_TEXT.OPERATION,
            width: '50',
            align: 'center',
            slots: {
              // default: ({ row, column }) => {
              //   return [
              //     <el-dropdown
              //       placement="bottom"
              //       size="small"
              //       onCommand={command => this.handleCommand(command, row)}
              //     >
              //       <span class="operation-column">{BTN_TEXT.OPERATION}</span>
              //       <el-dropdown-menu slot="dropdown">
              //         <el-dropdown-item class="operation-column" command="info">
              //           {' '}
              //           {BTN_TEXT.MODIFY}
              //         </el-dropdown-item>
              //         <el-dropdown-item
              //           class="operation-column"
              //           command="delete"
              //         >
              //           {BTN_TEXT.DELETE}
              //         </el-dropdown-item>
              //       </el-dropdown-menu>
              //     </el-dropdown>
              //     // <el-button onClick={() => this.showCreateTempModal(row)} type="text">
              //     //   {BTN_TEXT.MODIFY}
              //     // </el-button>,
              //     // <el-button onClick={() => this.deleteTemplate([row])} type="text">
              //     //   {BTN_TEXT.DELETE}
              //     // </el-button>
              //   ];
              // }
            }
          }
        ]
      },
      tempListData: [],
      tempDisableList: [],
      activeTemplate: null,
      // table加载动画
      relationShipTableDataLoading: false,
      // table数据
      relationShipTableData: [],
      showCreateTempRelation: false
    };
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 操作下拉事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.showCreateTempModal(row);
          break;
        case 'delete':
          var data = [];
          data.push(row);
          this.deleteTemplate(data);
          break;
        default:
          break;
      }
    },
    /**
     * @description: 获取模板数据
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    getTemplateData() {
      this.tableloading = true;
      warningTemplateGetList(
        {
          current: this.templateTableData.page.currentPage,
          size: this.templateTableData.page.pageSize
        },
        {}
      )
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.tableloading = false;
            this.templateTableData.page.total = res.total;
            this.templateTableData.data = res.rows;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
    },
    /**
     * @description: 显示模板模态框
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    showCreateTempModal(data) {
      this.templateEditTitle = '添加告警模板';
      this.showCreateTemp = true;
    },
    /**
     * @description: 显示模板模态框
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    showUpdateTempModal() {
      var row = {};

      if (!this.$refs.templateTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.templateTable.getRadioRecord();
      }

      this.isAddItem = false;
      this.selectTemplate = row;
      this.templateEditTitle = '修改告警模板';

      this.showCreateTemp = true;
    },
    /**
     * @description: 取消模态框
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    clickModalCancel() {
      this.showCreateTempRelation = false;
      this.showCreateTemp = false;
      this.isAddItem = true;
      this.selectTemplate = null;
    },
    cellClickEvent({ column }) {
    },
    cellClickEvent1({ column }) {
    },
    /**
     * @description: 新增或修改
     * @param data 数据
     * @return: 无
     * @author: ZJM+
     */
    addOrUpdateMaintainTemplate(data) {
      this.clickModalCancel();
      if (data.isAdd) {
        warningTemplateInsert(data.data)
          .then(res => {
            if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
              this.getTemplateData();
              showSuccessMessage(res.message);
            } else {
              showWarningMessage(res.message);
            }
          })
          .catch(err => {
            showErrorNotify(TITLE.NOTIFY, err);
          });
      } else {
        warningTemplateUpdateById(data.data)
          .then(res => {
            if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
              this.getTemplateData();
              showSuccessMessage(res.message);
            } else {
              showWarningMessage(res.message);
            }
          })
          .catch(err => {
            showErrorNotify(TITLE.NOTIFY, err);
          });
      }
    },
    /**
     * @description: 删除模板
     * @param data 数据
     * @return: 无
     * @author: ZJM
     */
    deleteTemplate() {
      var row = {};

      if (!this.$refs.templateTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.templateTable.getRadioRecord();
      }
      // if (data.length === 0) {
      //   this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      //   return false;
      // }
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteTemplate([row]);
        })
        .catch(() => {});
    },
    /**
     * @description: 执行删除模板
     * @param data 数据
     * @return: 无
     * @author: ZJM
     */
    setDeleteTemplate(data) {
      var ids = [];
      data.forEach(item => {
        ids.push(item.templateId);
      });

      warningTemplateDelete({ ids: ids })
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.getTemplateData();
            this.activeTemplate = null;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
    },
    /**
     * @description: 右边保养项关联表
     * @param data 数据
     * @return: 无
     * @author: ZJM
     */
    handleSelectTemplate(data) {
      this.activeTemplate = data;
      this.activeTemplateId = data.templateId;
      this.getRelationshipItemByID(data.templateId);
    },
    /**
     * @description: 根据保养模板查询关联保养项
     * @param id 数据
     * @return: 无
     * @author: ZJM
     */
    getRelationshipItemByID(id) {
      this.relationShipTableDataLoading = true;
      var self = this;
      getPageWarningConfig({
        templateId: id
      })
        .then(res => {
          self.relationShipTableDataLoading = false;
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            self.relationShipTableData = res.rows;
            self.tempDisableList = [];
            res.rows.forEach(item => {
              self.tempDisableList.push(item.warnConfigId);
            });
            self.rowDrop();
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch(err => {
          self.relationShipTableDataLoading = false;
          showErrorNotify(TITLE.NOTIFY, err);
        });
    },
    /**
     * @description: 打开选择点检项modal
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    showCreateTempRelationModal() {
      warningConfigGetByConditionAction()
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.tempListData = res.rows;
            this.showCreateTempRelation = true;
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
    },
    /**
     * @description: 插入一条保养项
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    insertMaintainceItems(data) {
      var self = this;
      data.forEach(item => {
        item.templateId = self.activeTemplate.templateId;
        self.relationShipTableData.push(item);
        self.tempDisableList.push(item.warnConfigId);
      });
      // this.tempDisableList = { ...this.relationShipTableData };
      this.clickModalCancel();
    },
    /**
     * @description: 保存保养项排序
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitTempRelation() {
      if (this.relationShipTableData.length === 0) {
        showWarningMessage('请添加检测项');
        return false;
      }
      var warnConfigIds = [];
      for (var i = 0; i < this.relationShipTableData.length; i++) {
        warnConfigIds.push(this.relationShipTableData[i].warnConfigId);
      }
      insertWarningTemplateConfigRelationships({
        templateId: this.activeTemplateId,
        warnConfigIds: warnConfigIds
      })
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getRelationshipItemByID(this.activeTemplate.templateId);
          } else {
            showWarningMessage(res.message);
          }
        })
        .catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
    },
    /**
     * @description: 删除保养项
     * @param data
     * @return: 无
     * @author: ZJM
     */
    deleteTemplateRelationItem() {
      var data = {};

      if (!this.$refs.sortTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        data = this.$refs.sortTable.getRadioRecord();
      }
      if (data.relationshipId) {
        warningTemplateConfigRelationshipDeleteById({
          id: data.relationshipId
        })
          .then(res => {
            if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
              showSuccessMessage(res.message);
              this.getRelationshipItemByID(this.activeTemplate.templateId);
            } else {
              showWarningMessage(res.message);
            }
          })
          .catch(err => {
            showErrorNotify(TITLE.NOTIFY, err);
          });
      } else {
        var index = 0;
        for (var i = 0; i < this.relationShipTableData.length; i++) {
          if (
            this.relationShipTableData[i].warnConfigId === data.warnConfigId
          ) {
            index = i;
            break;
          }
        }
        this.tempDisableList.splice(index, 1);
        this.relationShipTableData.splice(index, 1);
      }
    },
    /**
     * @description: 表拖拽
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    rowDrop() {
      var self = this;
      this.$nextTick(() => {
        var xTable = this.$refs.sortTable;
        this.sortable = Sortable.create(
          xTable.$el.querySelector('.body--wrapper>.vxe-table--body tbody'),
          {
            handle: '.drag-btn',
            onEnd: ({ newIndex, oldIndex }) => {
              var currRow = self.relationShipTableData.splice(oldIndex, 1)[0];
              self.relationShipTableData.splice(newIndex, 0, currRow);
            }
          }
        );
      });
    },
    /**
     * @description: 分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    handleTemplateTablePageChange(currentPage, pageSize) {
      this.templateTableData.page.currentPage = currentPage;
      this.templateTableData.page.pageSize = pageSize;
      this.getTemplateData();
    },
    /**
     * @description: table选中数据
     * @param records 数据
     * @return: 无
     * @author: ZJM
     */
    checkboxChangeEvent(records) {
      this.selectRecords = records;
    },
    refreshTable() {
      this.getTemplateData();
    }
  },
  watch: {
    relationShipTableData(val, oldVal) {
    }
  },
  created() {
    this.getTemplateData();
  },
  beforeDestroy() {
    if (this.sortable) {
      this.sortable.destroy();
    }
  }
};
