<template>
  <el-dialog
    :class="['create-maintaince-template',$store.state.dialogMarginClass]"
    width="30%"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content">
      <el-form
        ref="updateForm"
        :label-width="'100px'"
        :model="modalData"
        :rules="rulesUpdate"
        class="dialogForm updateForm"
      >
        <el-form-item :label="CONSTANTS_CHECK.TEMPLATE_NAME+':'" prop="templateName">
          <el-input v-model="modalData.templateName"></el-input>
        </el-form-item>
        <!-- <el-form-item :label="CONSTANTS_CHECK.SCENES_NAME+':'" prop="scenesId">
          <el-select
            v-model="modalData.scenesId"
            class="checkpoint-data"
            :loading="dropdownLoading"
            :placeholder="MSG.PLEASE_CHOOSE"
          >
            <el-option
              v-for="(item,index) in scenes"
              :key="index"
              :label="item.scenesName"
              :value="item.scenesId"
            ></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item :label="CONSTANTS_CHECK.REMARK+':'" prop="templateRemark" >
          <el-input
            class="checkpoint-data"
            type="textarea"
            :rows="4"
            v-model="modalData.templateRemark"
          ></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="confirmAction">{{confirmLabel}}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
    </div>
  </el-dialog>
</template>
<script>
// 导入文字常量
import {
  CONSTANTS_CHECK,
  MSG,
  BTN_TEXT,
  STATUS_CODE
} from '@/utils/constantsFile';
// API
import { scenesGetPageScenesAction } from '@/api/checkConfig.js';
export default {
  name: 'createMaintainceTemplateModal',
  props: {
    // 模态框名字
    title: { type: String, default: '' },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 是添加操作还是修改操作
    isAdd: { type: Boolean, default: true },
    // 数据
    data: { type: Object, default: () => {} },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      MSG: MSG,
      CONSTANTS_CHECK: CONSTANTS_CHECK,
      // 加载下拉数据
      dropdownLoading: false,
      scenes: [],
      // 模态框数据
      modalData: {
        templateRemark: '',
        templateName: '',
        templateId: null,
        scenesId: null
      },
      rulesUpdate: {
        templateName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 10, message: '长度不得大于255个字符' }
        ],
        templateRemark: [
          { max: 100, message: '长度不得大于255个字符' }
        ],
        scenesId: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }]
      }
    };
  },
  watch: {
    /**
     * @description: 监听数据
     * @param val //当前值
     * @return: 无
     * @author: ZJM
     */
    data(val, oldVal) {
      this.initData();
    },
    /**
     * @description: 监听数据
     * @param val //当前值
     * @return: 无
     * @author: ZJM
     */
    showDialog(val) {
      if (!val) {
        this.$refs.updateForm.resetFields();
      } else {
        setTimeout(() => {
          this.$refs.updateForm.clearValidate();
        }, 0);
      }
    }
  },
  methods: {
    /**
     * @description: 初始化数据
     * @return: void
     */
    initData: function() {
      this.scenes = [];
      if (!this.isAdd) {
        this.modalData.templateRemark = this.data.templateRemark;
        this.modalData.templateName = this.data.templateName;
        this.modalData.templateId = this.data.templateId;
        this.modalData.scenesId = this.data.scenesId;
      }
      this.dropdownLoading = true;
      scenesGetPageScenesAction().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.dropdownLoading = false;
          this.scenes = res.rows;
        }
      });
    },
    /**
     * @description: 处理点击取消
     * @return: void
     */
    cancelAction: function() {
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
     */
    confirmAction: function() {
      this.$refs.updateForm.validate(valid => {
        // 若验证通过
        if (valid) {
          var res = {
            isAdd: this.isAdd,
            data: this.modalData
          };
          this.handleConfirm(res);
          this.clearData();
        }
      });
    },
    /**
     * @description: 处理点击关闭
     * @return: void
     */
    closeAction: function() {
      if (this.handleClose) {
        this.clearData();
        this.handleClose();
      }
    },
    /**
     * @description: 处理选择的周期
     * @param {value} data 选中的周期
     * @return: void
     */
    handleCycleChange(data) {
      this.modalData.cycleName = data.cycleName;
      this.modalData.cycleId = data.cycleId;
    },
    /**
     * @description: 清空数据
     * @return: void
     */
    clearData() {
      this.modalData = {
        templateRemark: '',
        templateName: '',
        templateId: null,
        scenesId: null
      };
    }
  },
  created() {
    this.initData();
  }
};
</script>
