// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入提示组件
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 引入类型信息组件
import addAlarm from '@/views/main/AlarmConfiguration/addAlarm.vue';
// 搜索组建
import { exportWarningConfigTemplate } from '@/api/export';
import { exportImportWarningConfig } from '@/api/import';
import SearchBox from '@/components/common/SearchBox';
import ImportDialog from '@/components/common/ImportDialog';
import {
  STATUS_CODE,
  TITLE,
  MSG,
  VARIABLE,
  BTN_TEXT,
  CONSTANTS_ALARM_CONFIGURATION
} from '@/utils/constantsFile';
// 导入所需接口
import {
  warningConfigGetByConditionAction,
  warningConfigDeleteByIdsAction,
  warningConfigInsertOrUpdateBatchAction
} from '@/api/alarmconfiguration';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable,
    ImportDialog,
    // 加载新增设备组件
    addAlarm,
    SearchBox
  },
  data() {
    return {
      isImportDialogShow: false,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_ALARM_CONFIGURATION: CONSTANTS_ALARM_CONFIGURATION,
      // 设备修改表单数据
      formUpdate: {},
      // 搜索框有关
      isSearchBoxShow: false,
      searchList: [],
      searchData: [],
      // 设备修改表单验证
      rulesUpdate: {
        warnCode: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 30, message: '长度不得大于30个字符' }
        ],
        warnDetail: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 50, message: '长度不得大于50个字符' }
        ],
        warnRemark: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 200, message: '长度不得大于200个字符' }
        ]
      },
      // table多选数据
      dataChecks: [],
      tableName: 'device',
      // 用于操作当前显示哪个组件 0：主页面 1：新增子节点参数
      showPage: 0,
      // 表格查询表单数据
      formParams: {
        model: null
      },
      // 模态框名称
      titlename: '',
      // 控制模态框修改
      dialogVisibleUpdate: false,
      // table加载动画
      tableLoading: false,
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          type: 'radio',
          align: 'center',
          visible: true,
          width: '50'
        },
        { type: 'checkbox', width: '50', align: 'center' },
        {
          field: 'warnCode',
          title: CONSTANTS_ALARM_CONFIGURATION.WARN_CODE,
          align: 'center'
        },
        {
          field: 'warnDetail',
          title: CONSTANTS_ALARM_CONFIGURATION.WARN_DETAIL,
          align: 'center'
        },
        {
          field: 'warnRemark',
          title: CONSTANTS_ALARM_CONFIGURATION.WARN_REMARK,
          align: 'center'
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '50',
          align: 'center',
          slots: {
            // default: ({ row, column }) => {
            //   return [
            //     <el-dropdown
            //       placement="bottom"
            //       size="small"
            //       onCommand={command => this.handleCommand(command, row)}
            //     >
            //       <span class="operation-column">{BTN_TEXT.OPERATION}</span>
            //       <el-dropdown-menu slot="dropdown">
            //         <el-dropdown-item class="operation-column" command="info">
            //           {' '}
            //           {BTN_TEXT.MODIFY}
            //         </el-dropdown-item>
            //         <el-dropdown-item class="operation-column" command="delete">
            //           {BTN_TEXT.DELETE}
            //         </el-dropdown-item>
            //       </el-dropdown-menu>
            //     </el-dropdown>
            //     // <el-button onClick={() => this.setUpdate(row)} type="text">
            //     //   {BTN_TEXT.MODIFY}
            //     // </el-button>,
            //     // <el-button onClick={() => this.setDelete(row)} type="text">
            //     //   {BTN_TEXT.DELETE}
            //     // </el-button>
            //   ];
            // }
          }
        }
      ]
    };
  },
  computed: {},
  created() {
    // 调用设备列表查询
    this.findList();
  },
  watch: {
    // 切换模态框显示和隐藏
    dialogVisibleUpdate(val) {
      if (val === false) {
        this.$refs.updateForm.resetFields();
      }
    }
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 操作下拉事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.setUpdate(row);
          break;
        case 'delete':
          this.setDelete(row);
          break;
        default:
          break;
      }
    },
    /**
     * @description: 点击批量删除
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    deteleall() {
      if (this.dataChecks.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      } else {
        var arrdelete = [];
        this.dataChecks.map(item => arrdelete.push(item.warnConfigId));
        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDeleteChecks(arrdelete);
          })
          .catch(() => {});
      }
    },
    /**
     * @description: 执行批量删除的方法
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async setDeleteChecks(param) {
      try {
        const res = await warningConfigDeleteByIdsAction({
          ids: param
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取批量勾选的数据
     * @param data // 勾选数据
     * @return: 无
     * @author: ZJM
     */
    tableChooseCheck(data) {
      this.dataChecks = data;
    },
    /**
     * @description: table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询所属参数
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };

        const res = await warningConfigGetByConditionAction(params, {
          conditionJson1: this.searchData.conditionArr1,
          operator: this.searchData.groupOperator,
          conditionJson2: this.searchData.conditionArr2
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 显示修改模态框
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    setUpdate() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.dialogVisibleUpdate = true;
      this.formUpdate = JSON.parse(JSON.stringify(row));
    },
    /**
     * @description: 删除参数
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    setDelete() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteChecks([row.warnConfigId]);
        })
        .catch(() => {});
    },
    // /**
    //  * @description: 切换显示组件
    //  * @param name 标签name
    //  * @return:无
    //  * @author: ZJM
    //  */
    // async togglePage(name) {
    //   // 若是新增参数
    //   if (name === 'addDevice') {
    //     // 显示子组件新增参数

    //     this.$emit('togglePage');
    //     // this.showPage = 1;
    //   }
    // },
    addWarnConfig() {
      this.$emit('togglePage');
    },
    /**
     * @description: 提交修改
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitUpdate() {
      this.$refs.updateForm.validate(valid => {
        // 若验证通过
        if (valid) {
          this.submitUpdateFunc();
        }
      });
    },
    async submitUpdateFunc() {
      try {
        const form = {};
        form.warnConfigId = this.formUpdate.warnConfigId;
        form.warnCode = this.formUpdate.warnCode;
        form.warnDetail = this.formUpdate.warnDetail;
        form.warnRemark = this.formUpdate.warnRemark;
        const res = await warningConfigInsertOrUpdateBatchAction({}, [form]);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          this.dialogVisibleUpdate = false;
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    // /**
    //  * @description: 返回
    //  * @param val 0为显示index组件
    //  * @return:
    //  * @author: ZJM
    //  */
    // goBack(val) {
    //   // 显示组件
    //   this.showPage = val;
    //   // 调用查询接口
    //   this.findList();
    // },
    /**
     * @description:显示导入模态框
     * @param {type}
     * @return:
     * @author: ZJM
     */
    importTable() {
      this.isImportDialogShow = true;
      setTimeout(() => {
        this.$refs.ImportDialog.isShow = true;
        this.$refs.ImportDialog.resetUploadProgressBar();
      }, 0);
    },
    /**
     * @description: 获取查询条件
     * @param 无
     * @return: 无
     * @author: CH
     */
    async getSearchList() {
      try {
        const res = await {};
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTemplate() {
      try {
        const res = await exportWarningConfigTemplate();
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, '告警项导入模板.xls');
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:点检项导入提交
     * @param {type}
     * @return:
     * @author: ZJM
     */
    submitImportFile(param) {
      const fileObj = param.file;
      const fd = new FormData();
      fd.append('file', fileObj);
      // 执行接口
      exportImportWarningConfig({}, fd, {
        onUploadProgress: event => {
          // eslint-disable-next-line prefer-const
          let uploadedPercentage = Math.floor(
            (event.loaded * 100) / event.total
          );
          // option.onProgress({ percent: uploadedPercentage });
          this.$refs.ImportDialog.uploadPercent = uploadedPercentage;
        }
      })
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            param.onSuccess(res);
            this.findList();
          } else {
            param.onError(res);
          }
        })
        .catch(err => {
          showWarningMessage(err);
          param.onError(err);
        });
    },
    closeImportDialog() {
      this.getCheckOptionData();
      this.isImportDialogShow = false;
    },
    refreshTable() {
      this.findList();
    }
  }
};
