<template>
  <el-container>
    <div class="main" v-if="showPage === 1">
      <addAlarm @goBack="goBack"></addAlarm>
    </div>
    <div class="main" v-show="showPage !== 1">
      <el-header>
        <HeaderTitle class="header-title"></HeaderTitle>
      </el-header>
      <el-main class="view mm">
        <el-row>
          <el-col :span="24">
            <el-tabs v-model="activeName" type="card" @tab-click="handleClick" class="mm-tabs">
              <el-tab-pane label="告警项配置" name="first">
                <el-row class="tab-details">
                  <alarmConfiguration
                    ref="alarmConfiguration"
                    @togglePage="togglePage"
                    v-if="table==1"
                  ></alarmConfiguration>
                </el-row>
              </el-tab-pane>
              <el-tab-pane label="告警模板" name="second">
                <el-row class="tab-details">
                  <alarmTemplate v-if="table==2"></alarmTemplate>
                </el-row>
              </el-tab-pane>
              <el-tab-pane label="设备类型关联" name="third">
                <el-row class="tab-details">
                  <deviceTypeAssociation v-if="table==3"></deviceTypeAssociation>
                </el-row>
              </el-tab-pane>
            </el-tabs>
          </el-col>
        </el-row>
      </el-main>
    </div>
  </el-container>
</template>

<script>
import js from '@/assets/js/main/AlarmConfiguration/index';
export default js;
</script>

<style lang="less" scoped>
@import url('~@/assets/less/main/AlarmConfiguration/index.less');
</style>
