// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
// import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// import { maintainceOptionService } from '@/services/maintainceOption.service';
// 点检项配置组件
import alarmConfiguration from '@/views/main/AlarmConfiguration/alarmConfiguration.vue';
import addAlarm from '@/views/main/AlarmConfiguration/addAlarm.vue';
// // 场景组件
import alarmTemplate from '@/views/main/AlarmConfiguration/alarmTemplate.vue';
// // 模板配置组件
import deviceTypeAssociation from '@/views/main/AlarmConfiguration/deviceTypeAssociation.vue';
// import { CONSTANTS_CHECK } from '@/utils/constantsFile';
export default {
  name: 'AlarmConfiguration',
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    // HeadOperationItem,
    // 保养项配置组件
    alarmConfiguration,
    addAlarm,
    // // 保养场景组件
    alarmTemplate,
    // // 保养模板配置组件
    deviceTypeAssociation
  },
  data() {
    return {
      // CONSTANTS_CHECK: CONSTANTS_CHECK,
      // title: CONSTANTS_CHECK.CHECK_MANAGEMENT,
      activeName: 'first',
      activeItem: null,
      showPage: 0,
      table: 1
    };
  },
  methods: {
    /**
     * @description: 切换tab
     * @param tab tab数据
     * @return:无
     * @author: ZJM
     */
    handleClick(tab, event) {
      if (tab.label === '告警项配置') {
        this.table = 1;
      } else if (tab.label === '告警模板') {
        this.table = 2;
      } else if (tab.label === '设备类型关联') {
        this.table = 3;
      }
    },
    initPage() {
      // this.getMaintainOptionData();
    },
    /**
     * @description: 切换显示组件
     * @param name 标签name
     * @return:无
     * @author: ZJM
     */
    async togglePage() {
      // 若是新增参数
      this.showPage = 1;
    },
    /**
     * @description: 返回
     * @param val 0为显示index组件
     * @return:
     * @author: ZJM
     */
    goBack(val) {
      // 显示组件
      this.showPage = val;
      // 调用查询接口
      this.$refs.alarmConfiguration.findList();
    }
  },
  mounted() {
    this.initPage();
  }
};
